.blog-page__content p { font-size: 16px; font-weight: 400; color: #ffffff; margin-bottom: 20px; padding-right: 150px; line-height: 1.5em; }

@media only screen and (max-width: 992px) { .blog-page__content p { padding-right: 0; } }

.blog-page__content h3 { font-size: 24px; font-weight: 500; color: #ffffff; padding-right: 150px; margin-top: 50px; margin-bottom: 20px; }

.contacts { padding-top: 200px; padding-bottom: 130px; background: url("../img/blog-bg.jpg") repeat-y center 70px; }

@media only screen and (max-width: 992px) { .contacts { padding-top: 120px; } }

@media only screen and (max-width: 768px) { .contacts { padding-top: 80px; padding-bottom: 50px; } }

@media only screen and (max-width: 576px) { .contacts { padding-bottom: 40px; } }

.contacts__content { padding: 0 100px; }

@media only screen and (max-width: 992px) { .contacts__content { padding: 0 50px; } }

@media only screen and (max-width: 768px) { .contacts__content { padding: 0 20px; } }

@media only screen and (max-width: 576px) { .contacts__content { padding: 0; } }

.contacts__content p { margin-bottom: 20px; line-height: 1.5em; }

@media only screen and (max-width: 576px) { .contacts__content p { font-size: 14px; } }

.contacts__content p:last-child { margin-bottom: 0; }

.reviews { background: url("../img/rewievs-bg.png") no-repeat right 100px; padding-top: 195px; padding-bottom: 150px; }

@media only screen and (max-width: 992px) { .reviews { padding-top: 150px; padding-bottom: 150px; } }

@media only screen and (max-width: 992px) { .reviews { background-position: right 0px; padding-top: 50px; padding-bottom: 50px; } }

.reviews__slide { padding-left: 5px; padding-right: 5px; }

.reviews__slide p { border-bottom: 1px solid #353434; padding-bottom: 30px; line-height: 1.5em; max-height: 285px; overflow: hidden; text-overflow: ellipsis; }

.reviews__author { display: flex; padding: 25px 0; align-items: center; }

.reviews__author img { margin-right: 30px; border-radius: 5px; }

.reviews__author strong { font-size: 12px; font-weight: 400; color: #5A9F4E; }

.reviews__author p { padding-bottom: 0; border: 0; font-size: 16px; font-weight: 500; line-height: 1.3em; }

@media only screen and (max-width: 576px) { .reviews__author p { font-size: 14px; } }

.reviews .slick-prev, .reviews .slick-next { position: absolute; display: block; height: 15px; width: 15px; line-height: 0; font-size: 0; cursor: pointer; margin-top: -10px; padding: 0; bottom: 40px; background: #2a2929; border: none; outline: none; z-index: 1; }

.reviews .slick-prev { right: 35px; }

.reviews .slick-prev::before { content: url("../img/arrow-left-gray.svg"); }

.reviews .slick-prev:hover::before { content: url("../img/arrow-left-green.svg"); }

.reviews .slick-next { right: 0; }

.reviews .slick-next::before { content: url("../img/arrow-right-gray.svg"); }

.reviews .slick-next:hover::before { content: url("../img/arrow-right-green.svg"); }

.reviews__befor-after { margin-left: 80px; }

.reviews__befor-after .twentytwenty-handle { background: #5A9F4E; border-color: #5A9F4E; }

.reviews__befor-after .twentytwenty-handle:after, .reviews__befor-after .twentytwenty-handle:before { background: #5A9F4E; box-shadow: none; }

.reviews .twentytwenty-before-label:before { content: none; }

.reviews .twentytwenty-after-label:before { content: none; }

.modal { margin-top: 5%; background: url("../img/modal.jpg") center no-repeat; background-size: cover; width: 1170px; }

@media only screen and (max-width: 1200px) { .modal { width: auto; } }

.modal_close { display: inline-block; cursor: pointer; position: relative; top: 10px; right: 20px; left: 97%; }

.modal__content { padding-top: 120px; padding-left: 120px; padding-right: 200px; padding-bottom: 80px; }

@media only screen and (max-width: 1200px) { .modal__content { padding: 120px 0 80px; } }

@media only screen and (max-width: 768px) { .modal__content { padding-top: 50px; padding-bottom: 50px; } }

.modal h3 { padding-top: 15px; font-size: 36px; font-weight: 700; color: #ffffff; text-transform: uppercase; max-width: 280px; line-height: 1em; margin-bottom: 50px; }

@media only screen and (max-width: 768px) { .modal h3 { margin-bottom: 30px; } }

.modal p { font-size: 16px; font-weight: 400; color: #cccccc; max-width: 290px; line-height: 1.5em; margin-bottom: 30px; }

.modal .form { display: flex; flex-direction: column; }

.modal .form input { border: none; border-bottom: 1px solid #cccccc; background: transparent; line-height: 3; border-radius: 0; font-size: 16px; font-weight: 400; font-family: "Montserrat", sans-serif; color: #ffffff; outline: none; box-shadow: none; -webkit-appearance: none; -moz-appearance: none; margin-bottom: 30px; }

.modal .form input:focus:required:invalid { border-color: #cccccc; color: #ffffff; font-size: 16px; line-height: 3; outline: none; box-shadow: none; -webkit-appearance: none; }

.modal .form input:required:valid { color: #ffffff; font-size: 16px; line-height: 3; outline: none; box-shadow: none; -webkit-appearance: none; }

.modal .form input::placeholder { line-height: 3; color: #ffffff; font-size: 16px; font-family: "Montserrat", sans-serif; font-weight: 400; }

.modal .form label.error { transform: translateX(0) translateY(-25px); background: #5A9F4E; padding: 5px 10px; color: #ffffff; border-radius: 15px; width: 100%; }

.modal .form label.error::before { content: ''; position: absolute; width: 10px; height: 10px; background: #5A9F4E; top: -5px; left: 20px; transform: translateX(-50%) rotate(45deg); }

.modal .form .button { margin-top: 20px; margin-bottom: 40px; display: inline-block; position: relative; color: #ffffff; font-weight: 600; background: #5A9F4E; border: 3px solid #5A9F4E; transition: all .3s ease; font-size: 24px; padding: 26px 65px; border-radius: 45px; max-width: 270px; }

@media only screen and (max-width: 768px) { .modal .form .button { margin: 20px auto 40px; padding: 15px 65px; } }

.modal .form .button:hover { transition: all .3s ease; background: transparent; color: #5A9F4E; }

.modal__thanks { text-align: center; padding-top: 120px; padding-bottom: 80px; }

@media only screen and (max-width: 768px) { .modal__thanks { padding-top: 50px; padding-bottom: 50px; } }

.modal__thanks h3 { max-width: none; }

.modal__thanks p { max-width: none; }
